    <van-sku
      v-model="show"
      :sku="sku"
      :goods="goods"
      :goods-id="goodsId"
      :quota="quota"
      :quota-used="quotaUsed"
      :hide-stock="sku.hide_stock"
      :message-config="messageConfig"
      @buy-clicked="onBuyClicked"
      @add-cart="onAddCartClicked"
    />

    <van-goods-action>
      <van-goods-action-icon
        :color="goods.collected ? '#cfaa79' : ''"
        icon="star"
        @click="onClickStar"
      ></van-goods-action-icon>
      <van-goods-action-icon icon="cart-o" @click="onClickCart"></van-goods-action-icon>
      <van-goods-action-button type="warning" @click="onClickAddToCart"></van-goods-action-button>
      <van-goods-action-button type="danger" @click="onClickBugNow"></van-goods-action-button>
    </van-goods-action>
  </div>
