      sku: {
        // sku
        // 
        tree: [
          {
            k: "", // skuKeyName
            v: [
              {
                id: "30349", // skuValueId id
                name: "", // skuValueName
                imgUrl: "https://img.yzcdn.cn/public_files/2017/10/24/e5a5a02309a41f9f5def56684808d9ae.jpeg", // 
