              },
              {
                id: "1215",
                name: "",
                imgUrl: "https://img.yzcdn.cn/public_files/2017/10/24/1791ba14088f9c2be8c610d0a6cc0f93.jpeg",
                previewImgUrl: "https://img.yzcdn.cn/public_files/2017/10/24/1791ba14088f9c2be8c610d0a6cc0f93.jpeg"
              }
            ],
            k_s: "s1" // skuKeyStrsku  list key value  id
          }
        ],
        //  sku M  sku S 
        list: [
          {
            id: 2259, // skuId
            price: 100, // 
            s1: "1215", //  k_s  s1  id
            s2: "1193", //  k_s  s2  id
            s3: "0", // 30
            stock_num: 110 //  sku 
          }
        ],
        price: "1.00", // 
        stock_num: 227, // 
        collection_id: 2261, //  skuId  collection_id sku  id
        none_sku: false, // 
        messages: [
          {
            // 
            datetime: "0", //  time '1' 
            multiple: "0", //  text '1' 
            name: "", // 
            type: "text", // : id_no, text, tel, date, time, email
            required: "1", //  '1' 
            placeholder: "" // 
          }
        ],
        hide_stock: false // 
      },
      messageConfig: {

      }
    };
  },

  methods: {
    formatPrice() {
      return "" + (this.goods.price / 100).toFixed(2);
    },

    onClickStar() {
      let tips = "";
      this.goods.collected = !this.goods.collected;
      if (!this.goods.collected) {
        tips = "";
      }
      Toast(tips);
    },

    onClickCart() {
      this.$router.push("../cart");
    },

    onClickAddToCart() {
      this.show = true;
    },

    onClickBugNow() {
      this.show = true;
    },

    onBuyClicked() {
      this.show = false;
      this.$router.push('../order/add');
    },

    onAddCartClicked() {
      this.show = false;
      this.$router.push('../cart');
    }
  }
};
</script>

